.App-content {
  margin: 0 1em;
}

.App-logo {
  height: 5em;
}

.App-logo:hover {
  -webkit-animation: App-logo-spin infinite 1s cubic-bezier(0.1, 0.8, 1.0, 0.1);
          animation: App-logo-spin infinite 1s cubic-bezier(0.1, 0.8, 1.0, 0.1);
}

.App-header {
  text-align: center;
  background-color: darkgreen;
  height: 8em;
  padding: 1.2em;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  to { -webkit-transform: rotate(520deg); transform: rotate(520deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  to { -webkit-transform: rotate(520deg); transform: rotate(520deg); }
}

ul {
  position: relative;
  padding-left: 3em;
  margin-top: 2em;
}

ul::before {
  content: '';
  position: absolute;
  left: 0.5em;
  top: 1em;
  bottom: 1em;
  width: 0.2em;
  background-color: darkgreen;
}

ul li {
  list-style-type: none;
  position: relative;
}

ul li:not(:last-child) {
  margin-bottom: 2em;
}

ul li:before {
  content: '';
  position: absolute;
  border-radius: 100%;
  width: 1em;
  height: 1em;
  background-color: darkgreen;
  left: -2.9em;
  top: 0.1em;
}

ul li dl {
  display: -ms-flexbox;
  display: flex;
}

ul li dt {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

ul li dd {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 4 1;
      flex: 4 1;
}




